.socialContacts {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    aspect-ratio: 1;
    font-size: 1.5rem;
    color: #7a7a7a;
    border-radius: 50%;
    transition: color 0.5s ease-in-out;
}

@property --fill {
    syntax: "<percentage>";
    inherits: true;
    initial-value: 0%;
}

.link:hover {
    color: greenyellow;
    --fill: 100%;
}

.link::after {
    position: absolute;
    z-index: -1;
    content: "";
    inset: -3px;
    border-radius: inherit;
    background: conic-gradient(greenyellow var(--fill), transparent var(--fill));
    transition: background 0.6s ease-in-out;
}